.Carinformation-container{
    max-width: 100%;
    justify-content: center;
    justify-items: center;
    margin: 0%;
    padding: 0%;
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

.herosection-carinfomation{
    display: flex;
    width: 100%;
    justify-content:space-evenly;
    justify-items: center;

}

.car-information-image,
.car-information-firstpara{
    display: flex;
    white-space: nowrap;
    justify-content: center;
        flex-direction: column;
        align-items: center;
}
.car-image{
    width:85%
}
.coverage-item-car {
    display: flex;
    align-items: center;
    max-width: 500px;
    width: 100%;
    background-color: #f9f9f9;
    padding: 1rem;
    border-radius: 12px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.coverage-item-car img {
    width: 80px;
    height: auto;
    margin-right: 1rem;
}


.quotes-btn-carinformation {
    padding: 10px 20px;
    font-size: 14px;
    align-items: center;
    /* Responsive width */
    max-width: 150px;
    color: #fff;
    border-radius: 25px;
    background-color: #007bff;
    animation: blink 1.5s infinite;
    /* Optional: Prevent it from being too wide */
    margin: 15px auto 0 auto;
    /* Center the button */
    display: block;
    justify-content: center;
        /* Horizontally center */
        align-items: center;
        /* Vertically center */
        text-align: center;
    /* Required to make auto margins work */
}
@keyframes blink {
    0% {
        opacity: 1;
    }

    50% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}
@media (max-width: 768px) {
    .car-image {
            width: 70% !important;
            margin-left: -30% !important;
        }
}


@media (max-width: 768px) {
    .car-information-image{
        max-width: 70% !important;
        margin-right: -20%;
    }
}


@media (max-width: 768px) {
.private-car-insurance-heading{
    width:50%;
    margin-left: -40%;
    font-size: 18px;
    text-align: justify;

}
}

@media (max-width: 768px) {
.private-car-paragraph{
    text-align: justify;

    margin-left: 10%;
    margin-bottom: 10px;
    font-size: 12px;
    text-align: justify;
}
}

@media (max-width: 768px) {
    .car-image {
            width: 85% !important;
            margin-left: 20%;
        }
}
